@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro);
.promoCarousel {
  max-height: 50rem; }
  .promoCarousel .carousel-inner {
    max-height: 40rem;
    position: relative; }
  .promoCarousel .carousel-item {
    max-height: 40rem; }

.concert {
  position: relative;
  overflow: hidden;
  background: #1d1d1d;
  color: #fff; }
  .concert__image-container {
    width: 100%;
    height: 400px;
    overflow: hidden; }
    .concert__image-container__img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      -webkit-transition: .5s;
      transition: .5s;
      z-index: 2;
      position: relative; }
  .concert__info {
    margin: 2rem; }
    .concert__info__back {
      position: absolute;
      opacity: 0.4;
      top: 0;
      left: 0;
      -webkit-filter: grayscale(0.5) blur(2px); }
    .concert__info__performer {
      position: relative;
      z-index: 2; }
    .concert__info__genre {
      position: relative;
      z-index: 2; }
    .concert__info__day-n-price {
      position: relative;
      z-index: 2;
      margin: 0.5rem;
      position: absolute;
      bottom: 1rem;
      left: 1.5rem; }
  .concert:nth-of-type(2n) .row {
    color: #fff; }
    .concert:nth-of-type(2n) .row > div:first-child {
      order: 2; }
    .concert:nth-of-type(2n) .row > div:second-child {
      order: 1; }
  .concert:hover .concert__image-container__img {
    -webkit-transform: scale(1.1);
            transform: scale(1.1); }

@media (max-width: 768px) {
  .concert__info__performer {
    font-size: 1.8rem;
    margin: 0.5rem; }
  .concert__info__genre {
    font-size: 1.5rem;
    opacity: 0.7; } }

.order-ticket-form input,
.order-ticket-form select,
.order-ticket-form small {
  max-width: 25rem; }

.seats {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  max-width: 30rem; }
  .seats__seat {
    margin: 0.2rem;
    width: 2.5rem;
    height: 2.5rem; }

@media (max-width: 1200px) {
  .seats__seat {
    width: 2.2rem;
    height: 2.2rem;
    padding: 0 !important; } }

*, *::before, *::after {
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  color: #1d1d1d;
  background: #fff;
  font-family: "Source Sans Pro", sans-serif;
  font-size: 18.2px;
  line-height: 1.7;
  letter-spacing: 0.5px;
  overflow-y: scroll;
  min-height: 100vh; }

a, a:hover, a:visited {
  text-decoration: none; }

.container {
  margin: 1rem auto;
  min-height: calc(100vh - 14rem); }

h1, h2, h3, h4, h5, h6 {
  margin: 0.5rem; }

h1 {
  font-weight: bold;
  margin: 2rem 0;
  font-size: 4rem; }

h2 {
  font-weight: bold;
  margin: 2rem 0;
  font-size: 3rem; }

